import React from 'react';
import { Group, List, Cell, Avatar, Text, Div } from '@vkontakte/vkui';
import { useSelector, useDispatch } from 'react-redux';
import { getFriends } from 'core/selectors/friends';
import { AppDispatchActions, FriendItem } from 'core/models';
import { vkBridge } from 'core/vk-bridge/instance';

export const FriendsList = React.memo(() => {
  const { friendsCompleted, friendsNotCompleted } = useSelector(getFriends);
  const dispatch = useDispatch<AppDispatchActions>();

  const openFriendStat = React.useCallback(
    (friend: FriendItem) => {
      dispatch({
        type: 'SELECT_FRIEND',
        payload: {
          avatar: friend.avatar,
          name: friend.name,
          userId: friend.userId,
        },
      });
      vkBridge.send('VKWebAppEnableSwipeBack');
    },
    [dispatch]
  );

  const completedCells = friendsCompleted.map((friend) => (
    <Cell
      key={friend.userId}
      expandable
      onClick={() => openFriendStat(friend)}
      before={<Avatar size={40} src={friend.avatar} />}
    >
      {friend.name}
    </Cell>
  ));
  const notCompletedCells = friendsNotCompleted.map((friend) => (
    <Cell key={friend.userId} before={<Avatar size={40} src={friend.avatar} />}>
      {friend.name}
    </Cell>
  ));

  return (
    <>
      <Group separator="hide">
        <List>
          <Cell multiline>
            <Text weight="medium">Друзья, прошедшие тест</Text>
          </Cell>
          {completedCells}
        </List>
        <Div />
      </Group>
      <Group separator="hide">
        <List>
          <Cell multiline>
            <Text weight="medium">Друзья, не прошедшие тест</Text>
          </Cell>
          {notCompletedCells}
        </List>
      </Group>
    </>
  );
});
