import React from 'react';
import {
  Div,
  Group,
  Title,
  Text,
  List,
  SimpleCell,
  Switch,
  CellButton,
  Subhead,
} from '@vkontakte/vkui';
import { useFela } from 'react-fela';
import Icon28MessageAddBadgeOutline from '@vkontakte/icons/dist/28/message_add_badge_outline';
import Icon28MoneyCircleOutline from '@vkontakte/icons/dist/28/money_circle_outline';
import Icon28ClearDataOutline from '@vkontakte/icons/dist/28/clear_data_outline';
import { AppIcon } from 'assets/svg/AppIcon';
export const SettingsLayout = React.memo(() => {
  const { css } = useFela();

  return (
    <Div>
      <Group className={css({ textAlign: 'center' })}>
        <Div>
          <AppIcon className="" />
        </Div>
        <Title weight="bold" level="1" className={css({ marginBottom: '.5rem' })}>
          Тест на дружбу
        </Title>
        <Text weight="medium">Версия 666</Text>
      </Group>
      <Group>
        <List>
          <SimpleCell disabled after={<Switch defaultChecked />}>
            Уведомления
          </SimpleCell>
          <Div>
            <Subhead
              weight="regular"
              className={css({ color: 'var(--text_secondary)', margin: 0 })}
            >
              Мы будем сообщать о новых прохождениях твоего теста
            </Subhead>
          </Div>
          {/* TODO: actions */}
          <CellButton before={<Icon28MessageAddBadgeOutline />}>
            Связаться с разработчиками
          </CellButton>
          <CellButton before={<Icon28MoneyCircleOutline />}>
            Помочь проекту
          </CellButton>
          <CellButton mode="danger" before={<Icon28ClearDataOutline />}>
            Перепройти тест
          </CellButton>
        </List>
      </Group>
    </Div>
  );
});
