import React from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import { useSelector } from 'react-redux';
import { AppState, RootViewType } from 'core/models';
import { ConfigProvider, Root, View, Panel } from '@vkontakte/vkui';
import { Main } from 'roots/Main';
import { Quiz } from 'roots/Quiz';

const App = React.memo(() => {
  const rootView = useSelector((state: AppState) => state.ui.rootView);

  return (
    <ConfigProvider>
      <Root activeView={rootView}>
        <View id={RootViewType.Main} activePanel={RootViewType.Main}>
          <Panel id={RootViewType.Main}>
            <Main />
          </Panel>
        </View>
        <View id={RootViewType.Quiz} activePanel={RootViewType.Quiz}>
          <Panel id={RootViewType.Quiz}>
            <Quiz />
          </Panel>
        </View>
      </Root>
    </ConfigProvider>
  );
});

export default App;
