import React from 'react';
import { Div, Group, Title, Button, Text } from '@vkontakte/vkui';
import { useFela } from 'react-fela';
import { useDispatch, useSelector } from 'react-redux';
import { AppDispatchActions, QuizView } from 'core/models';
import { NightQuiz } from 'assets/svg/NightQuiz';
import { isThemeDrak } from 'core/selectors/common';

export const QuizStart = React.memo(() => {
  const { css } = useFela();
  const dark = useSelector(isThemeDrak);
  const dispatch = useDispatch<AppDispatchActions>();

  const goToQuizProcess = React.useCallback(() => {
    dispatch({ type: 'CHANGE_QUIZ_VIEW', payload: QuizView.Process });
  }, [dispatch]);
  return (
    <>
      <div className={css({ height: '50vh' })}>
        <div
          className={css({
            display: 'flex',
            flexDirection: 'column',
            height: '100%',
            overflow: 'hidden',
          })}
        >
          <NightQuiz
            className={css({
              display: 'flex',
              marginTop: 'auto',
              marginBottom: '2rem',
              alignSelf: 'center',
            })}
          />
        </div>
      </div>
      <Group
        separator="hide"
        className={css({ textAlign: 'center', padding: '0 1.5rem', height: 140 })}
      >
        <Div>
          <Title weight="bold" level="1">
            Один вопрос...
          </Title>
        </Div>
        <Div className={css({ marginBottom: '1.5rem' })}>
          <Text weight="medium">
            Хочешь пройти тест анонимно, или раскрыв свою личность?
          </Text>
        </Div>
      </Group>
      <Group separator="hide" className={css({ padding: '0 1.5rem' })}>
        <Div>
          <Button
            size="xl"
            mode="secondary"
            onClick={goToQuizProcess}
            className={css({
              backgroundColor: dark ? undefined : '#EDECFF',
              color: '#8881FF',
            })}
          >
            Публично
          </Button>
        </Div>
        <Div>
          <Button
            size="xl"
            mode="tertiary"
            onClick={goToQuizProcess}
            className={css({ color: dark ? '#595959' : '#D4D1FF' })}
          >
            Хочу остаться в тайне
          </Button>
        </Div>
      </Group>
    </>
  );
});
