import { createSelector, createStructuredSelector } from 'reselect';
import { getState } from './common';

export const getFriendsState = createSelector(getState, (ui) => ui.friends);

export const getFriendsCompletedTest = createSelector(getFriendsState, (state) =>
  state.list.filter((f) => f.completeTest)
);
export const getFriendsNotCompletedTest = createSelector(getFriendsState, (state) =>
  state.list.filter((f) => !f.completeTest)
);

export const getFriends = createStructuredSelector({
  friendsCompleted: getFriendsCompletedTest,
  friendsNotCompleted: getFriendsNotCompletedTest,
});

export const getSelectedFriend = createSelector(
  getFriendsState,
  state => state.selected
);

export const getFriendsView = createSelector(
  getFriendsState,
  state => state.view
);

export const getFriendsViewHistory = createSelector(
  getFriendsState,
  state => state.history
);
