export type FriendItem = {
  avatar: string;
  name: string;
  userId: number;
  completeTest: boolean;
};

export type SelectedFriend = {
  quiz?: {
    questions: {
      question: string;
      answer: string;
      userAnswer: string;
    };
  };
} & Omit<FriendItem, 'completeTest'>;

export enum FriendsView {
  List = 'list',
  SelectedFriend = 'selectedFriend',
}
