import React from 'react';
import { Group, Div, Avatar, Title, Button, CardGrid, Card } from '@vkontakte/vkui';
import { useSelector } from 'react-redux';
import { getSelectedFriend } from 'core/selectors/friends';
import { useFela } from 'react-fela';
import Icon24LogoInstagram from '@vkontakte/icons/dist/24/logo_instagram';
import Icon24ShareOutline from '@vkontakte/icons/dist/24/share_outline';

export const SelectedFriend = React.memo(() => {
  const selectedFriend = useSelector(getSelectedFriend);
  const { css } = useFela();
  if (!selectedFriend) {
    // TODO: no data or wait for data
    return null;
  }

  return (
    <>
      <Group separator="hide" className={css({ textAlign: 'center' })}>
        <Div
          className={css({
            display: 'flex',
            width: 56,
            margin: 'auto',
            marginTop: '2rem !important',
          })}
        >
          <Avatar size={56} src={selectedFriend.avatar} />
        </Div>
        <Title weight="medium" level="2" className={css({ margin: '.5rem 0' })}>
          {selectedFriend.name}
        </Title>
        <Div className={css({ display: 'flex' })}>
          <Button
            className={css({
              width: '100%',
              marginRight: '.75rem',
              borderRadius: '38px',
            })}
            mode="secondary"
            before={<Icon24LogoInstagram />}
            size="l"
          >
            Рассказать в истории
          </Button>
          <Button
            mode="secondary"
            size="l"
            className={css({ borderRadius: '38px', padding: '0 .5rem' })}
          >
            <Icon24ShareOutline />
          </Button>
        </Div>
      </Group>
      <Group separator="hide">
        <CardGrid className={css({ marginTop: '0 !important' })}>
          <Card size="l">
            <div className={css({ height: '100%', minHeight: 200 })} />
          </Card>
        </CardGrid>
      </Group>
    </>
  );
});
