import React from 'react';
import { QuizPreview, QuizStart, QuizProcess, QuizFinish } from 'modules/quiz';
import { useSelector } from 'react-redux';
import { getQuizView } from 'core/selectors/quiz';
import { QuizView } from 'core/models';
import { View, Panel } from '@vkontakte/vkui';

export const Quiz = React.memo(() => {
  const quiView = useSelector(getQuizView);

  return (
    <View activePanel={quiView}>
      <Panel id={QuizView.Preview}>
        <QuizPreview />
      </Panel>
      <Panel id={QuizView.Start}>
        <QuizStart />
      </Panel>
      <Panel id={QuizView.Process}>
        <QuizProcess />
      </Panel>
      <Panel id={QuizView.Finish}>
        <QuizFinish />
      </Panel>
    </View>
  );
});
