import React from 'react';
import { Div, PanelHeader } from '@vkontakte/vkui';
import { QuizItem } from './QuizItem';
import 'emoji-mart/css/emoji-mart.css';
import { useSelector } from 'react-redux';
import * as sel from 'core/selectors/quiz';

export const QuizProcess = React.memo(() => {
  const questionsDict = useSelector(sel.getQuizQuestionsDict);
  const count = useSelector(sel.getQuizQuestionsCount);
  const activeStep = useSelector(sel.getQuizActiveStep);

  return (
    <>
      <PanelHeader separator={false} />
      <Div>
        <QuizItem steps={count} {...questionsDict[activeStep]} />
      </Div>
    </>
  );
});
