import React from 'react';
import { Div, Group, CardGrid, Button, Text, Title } from '@vkontakte/vkui';
import { useFela } from 'react-fela';
import { Emoji } from 'emoji-mart';
import { Question, AppDispatchActions, QuizView } from 'core/models';
import { useDispatch, useSelector } from 'react-redux';
import { getQuizActiveStep } from 'core/selectors/quiz';
import { Slider } from './Slider';
import ScrollLock from 'react-scrolllock';

type Props = {
  steps: number;
} & Question;

export const QuizItem = React.memo<Props>(
  ({ firstAnswer, question, secondAnswer, step, steps, backgroundCard }) => {
    const { css } = useFela();
    const activeStep = useSelector(getQuizActiveStep);
    const dispatch = useDispatch<AppDispatchActions>();

    const nextStep = React.useCallback(() => {
      const nextStep = activeStep + 1;
      if (nextStep < steps) {
        setTimeout(() => {
          dispatch({ type: 'CHANGE_QUIZ_ACTIVE_STEP', payload: nextStep });
        }, 450);
      } else {
        setTimeout(() => {
          dispatch({ type: 'CHANGE_QUIZ_VIEW', payload: QuizView.Finish });
        }, 450);
      }
    }, [dispatch, activeStep, steps]);

    return (
      <Div>
        <ScrollLock accountForScrollbars>
          <span>
            <Group
              separator="hide"
              className={css({
                display: 'flex',
                justifyContent: 'center',
              })}
            >
              <CardGrid
                className={css({
                  position: 'relative',
                  maxWidth: 303,
                  height: '100%',
                  width: '100%',
                  padding: 0,
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                })}
              >
                <Slider
                  backgroundCard={backgroundCard}
                  question={question}
                  iconLeft={firstAnswer.emoji}
                  iconRight={secondAnswer.emoji}
                  onSwipeEnd={nextStep}
                />
              </CardGrid>
            </Group>
          </span>
        </ScrollLock>
        <Group
          separator="hide"
          className={css({
            marginTop: '5rem',
          })}
        >
          <Div
            className={css({
              display: 'flex',
              justifyContent: 'center',
            })}
          >
            <span
              className={css({
                marginRight: '4rem',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
              })}
            >
              <Button
                mode="secondary"
                size="xl"
                className={css({
                  width: 86,
                  height: 86,
                  borderRadius: '50%',
                })}
                onClick={nextStep}
              >
                <Emoji set="apple" emoji={firstAnswer.emoji} size={34} />
              </Button>
              <Text
                weight="medium"
                className={css({ textAlign: 'center', marginTop: '.75rem' })}
              >
                {firstAnswer.text}
              </Text>
            </span>
            <span
              className={css({
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
              })}
            >
              <Button
                mode="secondary"
                size="xl"
                className={css({
                  width: 86,
                  height: 86,
                  borderRadius: '50%',
                })}
                onClick={nextStep}
              >
                <Emoji set="apple" emoji={secondAnswer.emoji} size={34} />
              </Button>
              <Text
                weight="medium"
                className={css({ textAlign: 'center', marginTop: '.75rem' })}
              >
                {secondAnswer.text}
              </Text>
            </span>
          </Div>
        </Group>
        <Group separator="hide" className={css({ marginTop: '5rem' })}>
          <Div>
            <Title
              level="2"
              weight="regular"
              className={css({ color: '#CBCBCB', textAlign: 'center' })}
            >
              {step + 1}/{steps}
            </Title>
          </Div>
        </Group>
      </Div>
    );
  }
);
