import React from 'react';
import { Div, Group, Title, Button, Text, Subhead } from '@vkontakte/vkui';
import { useFela } from 'react-fela';
import Icon24LogoInstagram from '@vkontakte/icons/dist/24/logo_instagram';
import { getQuizQuestionsDict } from 'core/selectors/quiz';
import { useSelector } from 'react-redux';
import { Emoji } from 'emoji-mart';

export const QuizFinish = React.memo(() => {
  const questionsDict = useSelector(getQuizQuestionsDict);
  const { css } = useFela();
  return (
    <Div>
      <Group separator="hide" className={css({ textAlign: 'center' })}>
        <Div
          className={css({
            display: 'flex',
            margin: 'auto',
            marginTop: '4rem !important',
            color: '#727272',
            flexDirection: 'column',
          })}
        >
          <Title
            weight="bold"
            level="1"
            className={css({ margin: '.5rem 0', fontSize: '34px' })}
          >
            12/15
          </Title>
          <Text weight="medium" className={css({ margin: '.5rem 0' })}>
            Неплохо
          </Text>
        </Div>
        <Div className={css({ display: 'flex' })}>
          <Button
            className={css({
              width: '100%',
              borderRadius: '38px',
            })}
            mode="secondary"
            before={<Icon24LogoInstagram />}
            size="l"
          >
            Поделиться в истории
          </Button>
        </Div>
      </Group>
      <Group separator="hide">
        {Object.values(questionsDict).map((q) => (
          <Div key={q.step}>
            <Text weight="medium" className={css({ margin: '0 0 1rem' })}>
              {q.question}
            </Text>
            <div
              className={css({
                display: 'flex',
                alignItems: 'center',
              })}
            >
              <div
                className={css({
                  width: 48,
                  height: 48,
                  backgroundColor: q.step % 2 === 0 ? '#DFFFDE' : '#FFDEDE',
                  borderRadius: '50%',
                  border: '1px solid #EDEDED',
                  marginRight: '1rem',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                })}
              >
                <Emoji set="apple" emoji={q.firstAnswer.emoji} size={27} />
              </div>
              <Subhead
                weight="medium"
                className={css({ color: q.step % 2 === 0 ? '#71BF41' : '#DB6262' })}
              >
                Правильный ответ: {q.firstAnswer.text}
              </Subhead>
            </div>
          </Div>
        ))}
      </Group>
    </Div>
  );
});
