import React from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import { useSelector, useDispatch } from 'react-redux';
import { AppState, MainEpicViewType, AppDispatchActions } from 'core/models';
import { View, Panel, Epic, Tabbar, TabbarItem } from '@vkontakte/vkui';
import Icon28NewsfeedOutline from '@vkontakte/icons/dist/28/newsfeed_outline';
import Icon28Users from '@vkontakte/icons/dist/28/users_outline';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import { Home } from 'panels/Home';
import { Settings } from 'panels/Settings';
import { Friends } from 'panels/Friends';

export const Main = React.memo(() => {
  const mainEpicView = useSelector((state: AppState) => state.ui.mainEpicView);
  const dispatch = useDispatch<AppDispatchActions>();

  const goToHome = () =>
    dispatch({ type: 'CHANGE_MAIN_EPIC_VIEW', payload: MainEpicViewType.Home });
  const goToFriends = () =>
    dispatch({ type: 'CHANGE_MAIN_EPIC_VIEW', payload: MainEpicViewType.Friends });
  const goToSettings = () =>
    dispatch({ type: 'CHANGE_MAIN_EPIC_VIEW', payload: MainEpicViewType.Settings });

  return (
    <Epic
      activeStory={mainEpicView}
      tabbar={
        <Tabbar>
          <TabbarItem
            onClick={goToHome}
            selected={MainEpicViewType.Home === mainEpicView}
          >
            <Icon28NewsfeedOutline />
          </TabbarItem>
          <TabbarItem
            onClick={goToFriends}
            selected={MainEpicViewType.Friends === mainEpicView}
          >
            <Icon28Users />
          </TabbarItem>
          <TabbarItem
            onClick={goToSettings}
            selected={MainEpicViewType.Settings === mainEpicView}
          >
            <Icon28SettingsOutline />
          </TabbarItem>
        </Tabbar>
      }
    >
      <View id={MainEpicViewType.Home} activePanel={MainEpicViewType.Home}>
        <Panel id={MainEpicViewType.Home}>
          <Home />
        </Panel>
      </View>
      <View id={MainEpicViewType.Friends} activePanel={MainEpicViewType.Friends}>
        <Panel id={MainEpicViewType.Friends}>
          <Friends />
        </Panel>
      </View>
      <View id={MainEpicViewType.Settings} activePanel={MainEpicViewType.Settings}>
        <Panel id={MainEpicViewType.Settings}>
          <Settings />
        </Panel>
      </View>
    </Epic>
  );
});
