import { combineReducers, createStore, ReducersMapObject, Store } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import { initialState, reducer as uiReducer } from 'core/reducers/main';
import { AppState, AppDispatch } from 'core/models';

const rootReducerMap: ReducersMapObject<AppState, AppDispatch> = {
  ui: uiReducer,
};

export const store: Store<AppState, AppDispatch> = createStore<
  AppState,
  AppDispatch,
  unknown,
  unknown
>(combineReducers(rootReducerMap), { ui: initialState }, composeWithDevTools());
