import React from 'react';
import {
  Div,
  Group,
  CardGrid,
  Card,
  Title,
  Text,
  Button,
  Placeholder,
  Header,
} from '@vkontakte/vkui';
import { useFela } from 'react-fela';
import { Emoji } from 'emoji-mart';
import Icon28StatisticsOutline from '@vkontakte/icons/dist/28/statistics_outline';
import { useDispatch, useSelector } from 'react-redux';
import { AppDispatchActions, RootViewType } from 'core/models';
import { isThemeDrak } from 'core/selectors/common';

export const HomeLayout = React.memo(() => {
  const { css } = useFela();
  const dark = useSelector(isThemeDrak);
  const dispatch = useDispatch<AppDispatchActions>();

  const goToQuiz = React.useCallback(() => {
    dispatch({ type: 'CHANGE_ROOT_VIEW', payload: RootViewType.Quiz });
  }, [dispatch]);

  return (
    <Div>
      <Group>
        <CardGrid>
          <Card
            size="l"
            mode="shadow"
            className={css({ backgroundColor: dark ? '#2D2D2D' : '#E2E0FF' })}
          >
            <div
              className={css({
                height: 256,
                display: 'flex',
                flexDirection: 'column',
                color: dark ? undefined : '#827EB9',
              })}
            >
              <Div className={css({ paddingTop: '24px', paddingBottom: '8px' })}>
                <Emoji size={50} emoji="see_no_evil" />
              </Div>
              <Div>
                <Title weight="bold" level="1">
                  Пройдите тест
                </Title>
              </Div>
              <Div className={css({ paddingTop: 0 })}>
                <Text weight="medium">Это займёт минуту.</Text>
              </Div>
              <Div className={css({ marginTop: 'auto' })}>
                <Button
                  mode="secondary"
                  size="xl"
                  className={css({
                    width: '100%',
                    color: dark ? undefined : '#827EB9',
                  })}
                  onClick={goToQuiz}
                >
                  Пройти тест
                </Button>
              </Div>
            </div>
          </Card>
        </CardGrid>
      </Group>
      <Group header={<Header>Сатистика</Header>}>
        <Placeholder
          icon={
            <Icon28StatisticsOutline
              className={css({
                width: '50px !important',
                height: '50px !important',
                '>svg': { width: 50, height: 50 },
              } as any)}
            />
          }
          className={css({ height: 204 })}
        >
          Здесь будут результаты, а пока что эта иконка
        </Placeholder>
      </Group>
    </Div>
  );
});
