import React from 'react';
import { Div, Title, Text, Button, Group } from '@vkontakte/vkui';
import { useFela } from 'react-fela';
import { useDispatch, useSelector } from 'react-redux';
import { AppDispatchActions, RootViewType, QuizView } from 'core/models';
import { WhiteDanceQuiz } from 'assets/svg/WhiteDanceQuiz';
import { isThemeDrak } from 'core/selectors/common';

export const QuizPreview = React.memo(() => {
  const { css } = useFela();
  const dark = useSelector(isThemeDrak);
  const dispatch = useDispatch<AppDispatchActions>();

  const goToRootMain = React.useCallback(() => {
    dispatch({ type: 'CHANGE_ROOT_VIEW', payload: RootViewType.Main });
  }, [dispatch]);

  const goToQuizStart = React.useCallback(() => {
    dispatch({ type: 'CHANGE_QUIZ_VIEW', payload: QuizView.Start });
  }, [dispatch]);
  return (
    <>
      <Group separator="hide" className={css({ height: '48vh' })}>
        <Div
          className={css({
            display: 'flex',
            flexDirection: 'column',
            height: '100%',
          })}
        >
          <WhiteDanceQuiz
            className={css({
              display: 'flex',
              marginTop: 'auto',
              marginBottom: '2.5rem',
              alignSelf: 'center',
            })}
          />
        </Div>
      </Group>
      <Group
        separator="hide"
        className={css({ textAlign: 'center', padding: '0 1.5rem', height: 140 })}
      >
        <Div>
          <Title weight="bold" level="1">
            Хей!
          </Title>
        </Div>
        <Div className={css({ marginBottom: '1.5rem' })}>
          <Text weight="medium">
            Ты здесь, потому что хочешь доказать свою крепкую дружбу с Ильёй, не
            правда ли? Давай приступим!
          </Text>
        </Div>
      </Group>
      <Group separator="hide" className={css({ padding: '0 1.5rem' })}>
        <Div>
          <Button
            size="xl"
            mode="secondary"
            onClick={goToQuizStart}
            className={css({
              backgroundColor: dark ? undefined : '#EDECFF',
              color: '#8881FF',
            })}
          >
            Да!
          </Button>
        </Div>
        <Div>
          <Button
            size="xl"
            mode="tertiary"
            onClick={goToRootMain}
            className={css({ color: dark ? '#595959' : '#D4D1FF' })}
          >
            Выйти на главную
          </Button>
        </Div>
      </Group>
    </>
  );
});
