import React from 'react';
import {
  PanelHeader,
  View,
  Panel,
  PanelHeaderBack
} from '@vkontakte/vkui';
import { useFela } from 'react-fela';
import { FriendsList, SelectedFriend } from 'modules/friends';
import { useSelector, useDispatch } from 'react-redux';
import { getFriendsView, getFriendsViewHistory } from 'core/selectors/friends';
import { FriendsView, AppDispatchActions } from 'core/models';
import { vkBridge } from 'core/vk-bridge/instance';

export const Friends = React.memo(() => {
  const view = useSelector(getFriendsView);
  const history = useSelector(getFriendsViewHistory);
  const dispatch = useDispatch<AppDispatchActions>();
  const { css } = useFela();

  const goToList = React.useCallback(() => {
    dispatch({ type: 'CHANGE_FRIENDS_VIEW', payload: FriendsView.List });
    vkBridge.send('VKWebAppDisableSwipeBack');
  }, [dispatch]);

  return (
    <>
      <View activePanel={view} onSwipeBack={goToList} history={history}>
        <Panel id={FriendsView.List}>
          <PanelHeader separator={false} className={css({ marginBottom: '2rem' })}>
            Друзья
          </PanelHeader>
          <FriendsList />
        </Panel>
        <Panel id={FriendsView.SelectedFriend}>
          <PanelHeader left={<PanelHeaderBack onClick={goToList} />}>
            Профиль
          </PanelHeader>
          <SelectedFriend />
        </Panel>
      </View>
    </>
  );
});
