import { Dispatch } from 'redux';
import { FriendItem, SelectedFriend, FriendsView } from './friends';
import { Question } from './quiz';

export type AppState = {
  ui: {
    theme: 'client_light' | 'space_gray' | '';
    rootView: RootViewType;
    mainEpicView: MainEpicViewType;
    quiz: {
      view: QuizView;
      activeStep: number;
      questions: {
        [step: number]: Question;
      };
    };
    friends: {
      list: FriendItem[];
      selected: SelectedFriend | null;
      view: FriendsView;
      history: FriendsView[];
    };
  };
};

export type AppDispatch =
  | { type: 'GET_FRIENDS'; payload: FriendItem[] }
  | { type: 'SET_THEME'; payload: 'client_light' | 'space_gray' }
  | { type: 'SELECT_FRIEND'; payload: SelectedFriend }
  | { type: 'CHANGE_FRIENDS_VIEW'; payload: FriendsView }
  | { type: 'CHANGE_QUIZ_VIEW'; payload: QuizView }
  | { type: 'CHANGE_QUIZ_ACTIVE_STEP'; payload: number }
  | { type: 'CHANGE_ROOT_VIEW'; payload: RootViewType }
  | { type: 'CHANGE_MAIN_EPIC_VIEW'; payload: MainEpicViewType };

export type AppDispatchActions = Dispatch<AppDispatch>;

export enum RootViewType {
  Quiz = 'quiz',
  Main = 'main',
}

export enum MainEpicViewType {
  Friends = 'friends',
  Settings = 'settings',
  Home = 'home',
}

export enum QuizView {
  Preview = 'preview',
  Start = 'start',
  Process = 'process',
  Finish = 'finish',
}
