import * as models from 'core/models';
import { RootViewType, MainEpicViewType, QuizView, FriendsView } from 'core/models';
import { questions, list } from './mock';

export const initialState: models.AppState['ui'] = {
  theme: '',
  rootView: RootViewType.Main,
  mainEpicView: MainEpicViewType.Home,
  quiz: {
    activeStep: 0,
    questions,
    view: QuizView.Preview,
  },
  friends: {
    list,
    selected: null,
    view: FriendsView.List,
    history: [FriendsView.List],
  },
};

export const reducer = (
  state = initialState,
  dispatch: models.AppDispatch
): models.AppState['ui'] => {
  switch (dispatch.type) {
    case 'CHANGE_ROOT_VIEW': {
      return {
        ...state,
        rootView: dispatch.payload,
      };
    }
    case 'CHANGE_MAIN_EPIC_VIEW': {
      return {
        ...state,
        mainEpicView: dispatch.payload,
      };
    }
    case 'CHANGE_QUIZ_VIEW': {
      return {
        ...state,
        quiz: {
          ...state.quiz,
          view: dispatch.payload,
        },
      };
    }
    case 'GET_FRIENDS': {
      return {
        ...state,
        friends: {
          ...state.friends,
          list: dispatch.payload,
        },
      };
    }
    case 'SELECT_FRIEND': {
      return {
        ...state,
        friends: {
          ...state.friends,
          selected: dispatch.payload,
          view: FriendsView.SelectedFriend,
          history: [...state.friends.history, FriendsView.SelectedFriend],
        },
      };
    }
    case 'CHANGE_FRIENDS_VIEW': {
      return {
        ...state,
        friends: {
          ...state.friends,
          view: dispatch.payload,
          history: state.friends.history.filter((h) => h === dispatch.payload),
        },
      };
    }
    case 'CHANGE_QUIZ_ACTIVE_STEP': {
      return {
        ...state,
        quiz: {
          ...state.quiz,
          activeStep: dispatch.payload,
        },
      };
    }
    case 'SET_THEME': {
      return {
        ...state,
        theme: dispatch.payload
      };
    }

    default: {
      return state;
    }
  }
};
