import { createSelector } from 'reselect';
import { getState } from './common';

export const getQuizState = createSelector(getState, (ui) => ui.quiz);

export const getQuizView = createSelector(getQuizState, (quiz) => quiz.view);

export const getQuizActiveStep = createSelector(
  getQuizState,
  state => state.activeStep
);

export const getQuizQuestionsDict = createSelector(
  getQuizState,
  state => state.questions
);
export const getQuizQuestionsCount = createSelector(
  getQuizQuestionsDict,
  questions => Object.values(questions).length
)
