import React from 'react';
import { useSpring, animated, interpolate } from 'react-spring';
import { useGesture } from 'react-with-gesture';
import { useFela } from 'react-fela';
import { Title } from '@vkontakte/vkui';
import { Emoji } from 'emoji-mart';

// TODO: SLIDER SHOLD APPEAR NEW CARD ON SWIPE
// TODO: MAKE IT WITHOUT SCROLL LOCK
// TODO: refactor

export function Slider({
  question,
  backgroundCard,
  iconRight,
  iconLeft,
  onSwipeEnd,
}) {
  const { css } = useFela();
  const [
    bind,
    {
      delta: [xDelta],
      down,
    },
  ] = useGesture();

  const { x, size, rotate, iconLeftMove, iconRightMove } = useSpring<any>({
    x: down ? xDelta : 0,
    size: down ? 1.1 : 1,
    immediate: (name) => down && name === 'x',
    rotate: down && xDelta > 0 ? 7 : down && xDelta < 0 ? -7 : 0,
    iconLeftMove: down && xDelta > 0 ? -35 : 0,
    iconRightMove: down && xDelta < 0 ? 35 : 0,
  } as any);

  React.useEffect(() => {
    if (!down && Math.abs(xDelta) >= 41 && onSwipeEnd) {
      onSwipeEnd();
    }
  }, [down, xDelta]);

  return (
    <animated.div
      {...bind()}
      className={css({
        position: 'relative',
        width: 'calc(100% - 40px)',
        height: '240.56px',
        borderRadius: '10px',
        pointerEvents: 'auto',
        transformOrigin: '50% 50% 0px',
        marginTop: '5rem',
        background: 'linear-gradient(180deg, #A0C6FF 0%, #88CEF5 100%);',
      })}
    >
      <animated.div
        className={css({
          position: 'absolute',
          top: 80,
        })}
        style={{
          transform: interpolate([iconLeftMove], (x) => `translateX(${x}px)`),
        }}
      >
        <Emoji set="apple" emoji={iconLeft} size={56} />
      </animated.div>
      <animated.div
        className={css({
          position: 'absolute',
          top: 80,
          right: 0,
        })}
        style={{
          transform: interpolate([iconRightMove], (x) => `translateX(${x}px)`),
        }}
      >
        <Emoji set="apple" emoji={iconRight} size={56} />
      </animated.div>
      <animated.div
        className={css({
          background: backgroundCard
            ? backgroundCard
            : 'linear-gradient(180deg, #FFB7A0 0%, #FFB7A0 0.01%, #F5A188 100%);',
          color: '#000',
          display: 'flex',
          alignItems: 'center',
          position: 'absolute',
          width: '120%',
          height: '292px',
          top: '-70px',
          left: '-26px',
          borderRadius: '10px',
          boxShadow: '0px 6.81759px 11.6873px rgba(0, 0, 0, 0.09);',
          transition: 'ease-in-out',
        })}
        style={{
          transform: interpolate(
            [x, size, rotate],
            (x, s, r) =>
              `translateX(${x > 41 ? 41 : x < -41 ? -41 : x}px) rotate(${r}deg)`
          ),
        }}
      >
        <Title
          level="2"
          weight="semibold"
          className={css({
            padding: '1rem',
            textAlign: 'center',
            lineHeight: '50px',
          })}
        >
          {question}
        </Title>
      </animated.div>
    </animated.div>
  );
}
